body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background: #1C1B2F;
    color: #fff;
    text-align: center;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.header {
    font-size: 2.5em;
    margin-bottom: 20px;
}

.features {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.feature-item {
    margin: 0 20px;
    text-align: center;
}

.feature-item img {
    width: 50px;
    margin-bottom: 10px;
}

.timer {
    background: #2E2D48;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 30px;
}

.timer h2 {
    margin-bottom: 10px;
    color: #FFC107;
}

.timer div {
    font-size: 1.5em;
    font-weight: bold;
}

.buttons {
    margin-top: 20px;
}

.buttons button {
    background-color: #7F3EFA;
    border: none;
    padding: 15px 30px;
    color: white;
    font-size: 1em;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.buttons button:hover {
    background-color: #6631D9;
}
